import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { /*Link,*/ Redirect } from 'react-router-dom';
//import { toast } from 'react-toastify';
import { Button, Form, Row, Col, FormGroup, Input, CustomInput, Label } from 'reactstrap';
//import Divider from '../common/Divider';
//import SocialAuthButtons from './SocialAuthButtons';
import withRedirect from '../../hoc/withRedirect';

const LoginForm = ({ setRedirect, hasLabel, layout }) => {
  // State
  const [token, setToken] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(true);
  const [isDisabled, setIsDisabled] = useState(true);
  const [isSending, setIsSending] = useState(false)

  /*
  useEffect(() => {
    if (!isSending) {
      console.log(data);
      localStorage.setItem('token', data);
    }
  }, [data])
  */
/*
  // Handler
  const handleSubmit = e => {
    //e.preventDefault();
    //fetchData();
    //toast.success(`Logged in as ${email}`);
    //setRedirect(true);
  };
*/

  const fetchData = async () => {
    if (isSending) return;

    setIsSending(true);
    //let body = {'username': email, 'password': password};
    let body = {'username': 'email', 'password': 'pass'};
    const json_data = await fetch("http://127.0.0.1/twscan/login", {
      method: 'POST',
      body: JSON.stringify(body),
      headers: new Headers({'Content-Type': 'application/json'})
    }).then(response => {
        if (!response.ok) {
          throw new Error(response.statusText)
        }
        return response.json();
    })
    .catch(err => {
      console.log(err)
    })
    setToken(json_data.token);
    setIsSending(false);
  };

  useEffect(() => {
    setIsDisabled(!email || !password);
    if (token !== '') {
      localStorage.setItem('token', token);
      console.log(token);
    }
  }, [email, password, token]);

  // <Form onSubmit={() => { fetchData(); }}>
  return !isSending && token ? (<Redirect to={'/'} />) : (
    <Form>
      <FormGroup>
        {hasLabel && <Label>Email address</Label>}
        <Input
          placeholder={!hasLabel ? 'Email address' : ''}
          value={email}
          onChange={({ target }) => setEmail(target.value)}
          type="email"
        />
      </FormGroup>
      <FormGroup>
        {hasLabel && <Label>Password</Label>}
        <Input
          placeholder={!hasLabel ? 'Password' : ''}
          value={password}
          onChange={({ target }) => setPassword(target.value)}
          type="password"
        />
      </FormGroup>
      <Row className="justify-content-between align-items-center">
        <Col xs="auto">
          <CustomInput
            id="customCheckRemember"
            label="Remember me"
            checked={remember}
            onChange={({ target }) => setRemember(target.checked)}
            type="checkbox"
          />
        </Col>
        <Col xs="auto">
          {/*
          <Link className="fs--1" to={`/authentication/${layout}/forget-password`}>
            Forget Password?
          </Link>
          */}
        </Col>
      </Row>
      <FormGroup>
        <Button color="primary" block className="mt-3" disabled={isDisabled} onClick={() => {fetchData()}}>
          Log in
        </Button>
      </FormGroup>
        {/*
      <Divider className="mt-4">or log in with</Divider>
        {<SocialAuthButtons />}
        */}
    </Form>
  );
};

LoginForm.propTypes = {
  setRedirect: PropTypes.func.isRequired,
  layout: PropTypes.string,
  hasLabel: PropTypes.bool
};

LoginForm.defaultProps = {
  layout: 'basic',
  hasLabel: false
};

export default withRedirect(LoginForm);
