import React, { Fragment, useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, CardText, CardImg } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import PageHeader from '../components/common/PageHeader';
import img_cloud_data from '../assets/img/blog/cloud_data.jpg';
import img_wine_dist_alcohol from '../assets/img/blog/wine_dist_alcohol_2_samples.png';
import img_spark_headline_classification from '../assets/img/blog/spark_headline_classification.png';
import { cardStyle }  from '../helpers/myStyles';


const MLIndex = (props) => {
  const { queryName, deckName, location } = props
  const [cards, setCards] = useState([]);
  const [isSending, setIsSending] = useState(false)

  useEffect(() => {
      setCards([]);
  }, [location.pathname])

  return (
  <Fragment>

      <PageHeader
          title="Machine Learning Related"
          description=""
          className="mb-3" />

    <Row noGutters className="mt-3">

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_cloud_data} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Collaborative Filtering Recommender
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Kaggle ecommerce dataset</li>
                            <li>Use sklearn cosine_similarity </li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/similarity_recommender/recommender.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_wine_dist_alcohol} top style={{"background":"white"}} />
                <CardBody>
                    <CardTitle tag="h5">
                        Wine Quality Prediction
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Random forest model</li>
                            <li>Independence tests to find possible dependent variables</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/wine_quality_prediction_eda/README.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_spark_headline_classification} top style={{"background":"white"}} />
                <CardBody>
                    <CardTitle tag="h5">
                        Headlines Classification Using Spark
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>NaiveBayes</li>
                            <li>Spark standalone cluster</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/spark_headlines_classification/bayes_headline_classification.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

    </Row>


  </Fragment>
  );
};

export default MLIndex;
