import React, { Fragment, useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, CardText, CardImg } from 'reactstrap';
import { Row, Col } from 'reactstrap';
//import WidgetsSectionTitle from "./WidgetsSectionTitle";
//import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import PageHeader from '../components/common/PageHeader';
//import classNames from 'classnames';
import fetch from "cross-fetch";
//import FalconCardHeader from '../components/common/FalconCardHeader';
//import generic1 from '../assets/img/generic/1.jpg';
import img_flashcards from '../assets/img/blog/flashcards.jpg';
import img_computer_code from '../assets/img/blog/computer_code.jpg';
import img_tokyo_yamanote from '../assets/img/blog/tokyo_yamanote.png';
import img_tokyo_airbnb from '../assets/img/blog/tokyo_airbnb_prices.png';
import img_tokyo_photo from '../assets/img/blog/tokyo_photo_kde.png';
import { cardStyle }  from '../helpers/myStyles';


const BlogIndex = (props) => {
  const { queryName, deckName, location } = props
  const [cards, setCards] = useState([]);
  const [isSending, setIsSending] = useState(false)

  useEffect(() => {
      setCards([]);
  }, [location.pathname])

  const fetchData = async () => {

    if (isSending) return;
    setCards([]);
    setIsSending(true);

    const url = process.env.REACT_APP_DOMAIN + queryName;
    let card_data_list = await fetch(url,
        {
          method: 'GET',
          headers: new Headers({'Content-Type': 'application/json'})
        }).then(response => {
            console.log(response);
            if (!response.ok) {
                throw new Error(response.statusText)
            }
            return response.json();
        }).catch(err => {
          console.log(err);
          return false;
        });

    if (card_data_list === false) {
        console.log('error');
        setIsSending(false);
        return;
    }

    /*
    let cards = [];
    let count = 0;
    for (const data of card_data_list) {
      count = count + 1;
      cards.push(<Flashcard key={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} />);
    }
     */

    setCards(cards);
    setIsSending(false);
  }

  return (
  <Fragment>

      <PageHeader
          title="Code Demos"
          description=""
          className="mb-3" />

    <Row noGutters className="mb-8">

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_flashcards} top />
                <CardBody>
                    <CardTitle tag="h5">
                        jpflashcard.com
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Japanese vocabulary flashcards</li>
                            <li>Google Cloud Run fastapi backend</li>
                            <li>Google Cloud Run react frontend served from docker nginx</li>
                        </ul>
                        This is a simple flashcards react app that I did to help me with learning Japanese
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://jpflashcard.com")}>
                        open in new window
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_tokyo_airbnb} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Tokyo Airbnb Prices Dataset
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Airbnb prices for each ward</li>
                            <li>Folium with leaflet js</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/examples/blob/main/geo-spatial-data-visualization/tokyo-airbnb-price/app.py")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_tokyo_photo} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Tokyo Attractions Photo Dataset
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Point pattern analysis</li>
                            <li>Scatter plot, hexbin, kernel density estimation</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/examples/blob/main/geo-spatial-data-visualization/tokyo-photo-dataset/point_pattern_analysis.py")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_tokyo_yamanote} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Folium with LeafletJS Map
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Scraped the geo locations of Yamanote line stations</li>
                            <li>Folium with leaflet js</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/examples/blob/main/geo-spatial-data-visualization/tokyo-wards-folium/main.ipynb")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

      <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_computer_code} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Japanese Text Tokenizer
                    </CardTitle>
                    <CardText tag="p">
                        Use the fugashi library to tokenize Japanese text and categorize the different types of words for later use
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/jp-tokenizer")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
      </Col>

      <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_computer_code} top />
                <CardBody>
                    <CardTitle tag="h5">
                        React ToDoList App Testing
                    </CardTitle>
                    <CardText tag="p">
                        Wrote some react testcases using react testing-library for the popular tutorial ToDoList App project.
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/react-hooks-todo-testing")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
      </Col>

    </Row>

  </Fragment>
  );
};

export default BlogIndex;
