import React, { Fragment, useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, CardText, CardImg } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import PageHeader from '../components/common/PageHeader';
import img_fashion from '../assets/img/blog/fashion-mnist-sprite.png';
import img_glue from '../assets/img/blog/aws_glue.png';
import { cardStyle }  from '../helpers/myStyles';


const AmazonAWSIndex = (props) => {
  const { queryName, deckName, location } = props
  const [cards, setCards] = useState([]);
  const [isSending, setIsSending] = useState(false)

  useEffect(() => {
      setCards([]);
  }, [location.pathname])

  return (
  <Fragment>

      <PageHeader
          title="Amazon AWS Related"
          description="click on the button to display a rendered jupyter notebook on github"
          className="mb-3" />

    <Row noGutters className="mt-3">

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_fashion} top />
                <CardBody>
                    <CardTitle tag="h5">
                        CNN Fashion MNIST Classification
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>AWS Sagemaker</li>
                            <li>Fashion MNIST dataset</li></ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/sagemaker_tf_fashion_mnist/sagemaker-tf-train-fashion-mnist.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_glue} top />
                <CardBody>
                    <CardTitle tag="h5">
                        AWS Lambda and Glue ETL Bootstrap Script
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Create needed roles and policies for creating AWS Lambda Functions and Glue jobs</li>
                            <li>Trigger Glue crawler when bucket has file</li>
                            <li>Send CloudWatch event and trigger secondary Lambda to start ETL job run when crawler finishes</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/aws-s3-lambda/")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

    </Row>


  </Fragment>
  );
};

export default AmazonAWSIndex;
