import React, { Fragment, useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, CardText, CardImg } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import PageHeader from '../components/common/PageHeader';
import img_clusters from '../assets/img/blog/kmeans-clusters.png';
import img_cluster_shop from '../assets/img/blog/cluster_shop_amount.png';
import img_case_hr_retention from '../assets/img/blog/case_human_resources_retention.png';
import img_ab_testing_dist from '../assets/img/blog/ab_testing_dist.png';
import img_ab_testing_mobile_game from '../assets/img/blog/ab_testing_mobile_game.png';
import img_hypo_examples from '../assets/img/blog/hypo_examples.png';
import { cardStyle }  from '../helpers/myStyles';


const BusinessIndex = (props) => {
  const { queryName, deckName, location } = props
  const [cards, setCards] = useState([]);
  const [isSending, setIsSending] = useState(false)

  useEffect(() => {
      setCards([]);
  }, [location.pathname])

  return (
  <Fragment>

      <PageHeader
          title="Business Related Analysis"
          description=""
          className="mb-3" />

    <Row noGutters className="mt-3">

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_hypo_examples} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Hypothesis Testing Examples
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>3 different methods</li>
                            <li>by calculation</li>
                            <li>by python statistical package</li>
                            <li>by sampling distribution</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/ab_testing/hypothesis_examples/main.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_ab_testing_mobile_game} top />
                <CardBody>
                    <CardTitle tag="h5">
                        A/B Testing of Mobile Game Users Behavior
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Kaggle cookie cats dataset</li>
                            <li>Mann-Whitney U Test</li>
                            <li>Bootstrap method</li>
                            <li>1D/7D retention rate</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/ab_testing/mobile_game/app.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_clusters} top />
                <CardBody>
                    <CardTitle tag="h5">
                        KMeans Customer Segmentation
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>KMeans</li>
                            <li>Recency, Frequncy and Monetary</li>
                            <li>log transformation</li>
                            <li>Silhouette scoring</li>
                            <li>Snake plot</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/customer_segmentation/doc/notebook.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_ab_testing_dist} top />
                <CardBody>
                    <CardTitle tag="h5">
                        A/B Testing Marketing Performance
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>AB testing performance analysis</li>
                            <li>analysis of sample groups in dataset</li>
                            <li>Validate result by rejecting null hypothesis</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/ab_testing/marketing_promotions/ab_testing_marketing.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_cluster_shop} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Supermarket Customer Segmentation
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>KMeans</li>
                            <li>Supermarket dataset</li>
                            <li>Amount and distance clusters</li>
                            <li>Silhouette scoring</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/supermarket_customer_segmentation/run_analysis.py")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_case_hr_retention} top style={{"background":"white"}} />
                <CardBody>
                    <CardTitle tag="h5">
                        Employee Retention Prediction
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Random Forest, Logistic Regression, Tensorflow models</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/business/human_resources_retention/Human-Resources-Retention.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

    </Row>


  </Fragment>
  );
};

export default BusinessIndex;
