import React, { Fragment, useEffect, useState } from 'react';
import { Button, Collapse, Card, CardBody, CardColumns, CardTitle, CardText, Nav, NavItem, NavLink } from 'reactstrap';
//import { Row, Col } from 'reactstrap';
//import WidgetsSectionTitle from "./WidgetsSectionTitle";
//import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
//import PageHeader from '../components/common/PageHeader';
import classNames from 'classnames';
import fetch from "cross-fetch";
import FalconCardHeader from '../components/common/FalconCardHeader';
import { ReactFuri } from 'react-furi';


const Flashcard = (props) => {
    const {index, color, side_a, side_b, showFuri, data} = props
    const [toggle, setToggle] = useState(false);
    const [furiToggle, setFuriToggle] = useState(false);
    const [text, setText] = useState(side_a);

    useEffect(() => {
      setFuriToggle(showFuri);
    }, [showFuri]);

    function buildSideB(str) {
        str = str.replace(/\"/i, '');
        if (str.includes("\n") === true) {
            let parts = str.split("\n");
            if (parts.length > 1) {
                parts.shift();
                let lines = [];
                let count = 0;
                for (const meaning of parts) {
                    if (count == 0) {
                        lines.push(<Fragment>{meaning}<br/><br/></Fragment>);
                    } else {
                        lines.push(<Fragment>{meaning}<br/></Fragment>);
                    }
                    count = count + 1;
                }
                return (<Fragment>{lines}</Fragment>);
            } else {
                return (<Fragment>{str}</Fragment>);
            }
        } else {
            let parts = str.split(",");
            if (parts.length > 1) {
                parts.shift();
                parts = parts.join("").split(";");
                let lines = [];
                for (const meaning of parts) {
                    lines.push(<Fragment>{meaning}<br/></Fragment>);
                }
                return (<Fragment>{lines}</Fragment>);
            } else {
                return (<Fragment>{str}</Fragment>);
            }
        }
    }

    function getReading(side_b) {
        if (side_b.includes("\n") === true) {
            return side_b.split("\n")[0];
        } else {
            return side_b.split(",")[0];
        }
    }

    var sideB = buildSideB(side_b);
    var reading = getReading(side_b);

    function onClick() {
      if (toggle === false) {
        setText(side_b);
      } else {
        setText(side_a);
      }
      setToggle(!toggle);
    }

    //let latexString = "\\int_{a}^{b} f(x)dx = F(b) - F(a)";
    let latexString = "Var(X)";
    return (
        <Card body inverse color={color} className={classNames({'bg-dark':color === 'light'})} style={{cursor:"pointer"}}>
            <CardBody onClick={() => onClick()}>
                <CardTitle>
                    <div style={{color:"#4c535e", fontSize:"1rem"}}>{index}.</div>
                </CardTitle>
                <CardTitle>
                    <ReactFuri word={side_a} reading={reading} showFuri={furiToggle} />
                </CardTitle>
                <CardText>
                    {toggle? sideB : <Fragment></Fragment>}
                </CardText>
            </CardBody>
            <Button color="info" size="sm" onClick={() => setFuriToggle(!furiToggle)}>furi</Button>
            {data["N1"] && !data['N2'] && !data['N3']? <Button className="ml-2" color="primary" size="sm">N1</Button> : <></>}
            {data["N2"] && !data['N3'] ? <Button className="ml-2" color="warning" size="sm">N2</Button> : <></>}
            {data["N3"]? <Button className="ml-2" color="success" size="sm">N3</Button> : <></>}
        </Card>
    )
}


const FlashcardApp = (props) => {
  const { queryName, deckName, location } = props

  const [data, setData] = useState("");
  const [cards, setCards] = useState([]);
  const [flipToggle, setFlipToggle] = useState(false);
  const [furiToggle, setFuriToggle] = useState(false);
  const [n1Toggle, setN1Toggle] = useState(false);
  const [n2Toggle, setN2Toggle] = useState(false);
  const [n3Toggle, setN3Toggle] = useState(false);
  const [nAllToggle, setNAllToggle] = useState(false);
  const [isSending, setIsSending] = useState(false)

  useEffect(() => {
      setData("");
      setCards([]);

      setFlipToggle(false);
      setFuriToggle(false);
      setN1Toggle(false);
      setN2Toggle(false);
      setN3Toggle(false);
      setNAllToggle(true);

      getData();
  }, [location.pathname])

  function onClickN1() {
    setCards([]);
    let items = data["data"];
    let cards = [];
    let count = 0;
    for (const data of items) {
      count = count + 1;
      if (data['N1'] === 1 && !data['N2'] && !data['N3']) {
          cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={furiToggle} data={data} />);
      }
    }
    setCards(cards);

    setN1Toggle(true);
    setN2Toggle(false);
    setN3Toggle(false);
    setNAllToggle(false);
  }

  function onClickN2() {
    setCards([]);
    let items = data["data"];
    let cards = [];
    let count = 0;
    for (const data of items) {
      count = count + 1;
      if (data['N2'] === 1 && !data['N3']) {
          cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={furiToggle} data={data} />);
      }
    }
    setCards(cards);

    setN1Toggle(false);
    setN2Toggle(true);
    setN3Toggle(false);
    setNAllToggle(false);
  }

  function onClickN3() {
    setCards([]);
    let items = data["data"];
    let cards = [];
    let count = 0;
    for (const data of items) {
      count = count + 1;
      if (data['N3'] === 1) {
          cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={furiToggle} data={data} />);
      }
    }
    setCards(cards);

    setN1Toggle(false);
    setN2Toggle(false);
    setN3Toggle(true);
    setNAllToggle(false);
  }

  function onClickNAll() {
    setCards([]);
    let items = data["data"];
    let cards = [];
    let count = 0;
    for (const data of items) {
      count = count + 1;
      cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={furiToggle} data={data} />);
    }
    setCards(cards);
    setN1Toggle(false);
    setN2Toggle(false);
    setN3Toggle(false);
    setNAllToggle(true);
  }

  function onClick() {
    let toggle = !furiToggle;
    let items = data["data"];
    let cards = [];
    let count = 0;
    for (const data of items) {
      count = count + 1;
      if (nAllToggle === true) {
          cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={toggle} data={data} />);
      } else {
          if (n1Toggle && data['N1'] === 1 && !data['N2'] && !data['N3']) {
              cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={toggle} data={data} />);
          }
          if (n2Toggle && data['N2'] === 1 && !data['N3']) {
              cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={toggle} data={data} />);
          }
          if (n3Toggle && data['N3'] === 1) {
              cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={toggle} data={data} />);
          }
      }
    }
    setCards(cards);
    setFuriToggle(toggle);
  }

  const getData = async () => {
    //let dir = location.pathname.split('/').at(-2);
    //let data_file = location.pathname.split('/').at(-1);
    //let path = "/data/" + dir +"/" + data_file + ".json";
    let path = "/data/" + queryName + ".json";
    let json_data = await fetch(path,
        {
            headers : {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        }).then(function(response) {
            //console.log(response);
            return response.json();
        }).catch(err => {
          console.log(err);
          return false;
        });

    if (!json_data) {
        return false;
    }

    let items = json_data["data"];
    let cards = [];
    let count = 0;
    for (const data of items) {
      count = count + 1;
      cards.push(<Flashcard key={"key_"+count} index={count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={furiToggle} data={data}/>);
    }
    setCards(cards);
    setData(json_data);
  }

  const fetchData = async () => {

    if (isSending) return;
    setCards([]);
    setIsSending(true);

    const url = process.env.REACT_APP_DOMAIN + queryName;
    let card_data_list = await fetch(url,
        {
          method: 'GET',
          headers: new Headers({'Content-Type': 'application/json'})
        }).then(response => {
            console.log(response);
            if (!response.ok) {
                throw new Error(response.statusText)
            }
            return response.json();
        }).catch(err => {
          console.log(err);
          return false;
        });

    if (card_data_list === false) {
        console.log('error');
        setIsSending(false);
        return;
    }

    setData(card_data_list);

    let cards = [];
    let count = 0;
    for (const data of card_data_list) {
      count = count + 1;
      cards.push(<Flashcard key={"key_"+count} color={'light'} side_a={data['side_a']} side_b={data['side_b']} showFuri={furiToggle} />);
    }

    setCards(cards);
    setIsSending(false);
  }

  return (
  <Fragment>

    <FalconCardHeader title={"Flashcards - Japanese " + deckName + " - click on cards to reveal answers"} >
    </FalconCardHeader>

    <Card>
      <CardBody>

          <Nav pills>
              <NavItem>
                  <Button color="primary" className="ml-2" disabled={isSending} onClick={() => fetchData()} >Load Cards</Button>
              </NavItem>
              <Button color="primary" className="ml-2" disabled={isSending} onClick={() => onClick()} >Toggle Furigana</Button>
              <Button color="primary" className="ml-2" disabled={isSending} onClick={() => onClickNAll()} >All</Button>
              <Button color="primary" className="ml-2" disabled={isSending} onClick={() => onClickN1()} >N1</Button>
              <Button color="primary" className="ml-2" disabled={isSending} onClick={() => onClickN2()} >N2</Button>
              <Button color="primary" className="ml-2" disabled={isSending} onClick={() => onClickN3()} >N3</Button>
          </Nav>

          <p></p>

          <CardColumns>
              {cards ? cards : <Fragment></Fragment>}
          </CardColumns>

      </CardBody>
    </Card>

  </Fragment>
  );
};

export default FlashcardApp;
