import React, { Fragment, useEffect, useState } from 'react';
import { Button, Card, CardBody, CardTitle, CardText, CardImg } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import PageHeader from '../components/common/PageHeader';
import img_cats_and_dogs from '../assets/img/blog/cats_and_dogs.png';
import img_sign from '../assets/img/blog/sign_mnist.png';
import img_fashion from '../assets/img/blog/fashion-mnist-sprite.png';
import img_stackoverflow from '../assets/img/blog/stackoverflow.png';
import img_us_census from '../assets/img/blog/us_census.jpg';
import img_auto_mpg from '../assets/img/blog/auto_mpg.png';
import img_house from '../assets/img/blog/house_top_features.png';
import img_hackernews from '../assets/img/blog/hackernews.png';
import { cardStyle }  from '../helpers/myStyles';


const TensorflowIndex = (props) => {
  const { queryName, deckName, location } = props;
  const [cards, setCards] = useState([]);
  const [isSending, setIsSending] = useState(false);

  useEffect(() => {
      setCards([]);
  }, [location.pathname])

  return (
  <Fragment>

      <PageHeader
          title="Tensorflow Related"
          description="click on the button to display a rendered jupyter notebook on github"
          className="mb-3" />

    <Row noGutters className="mt-3">

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_house} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Advanced House Prices Prediction
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Kaggle advanced house dataset</li>
                            <li>Pipeline code to run combinations of parameters</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/house_prices/kaggle_advanced_dataset_md/advanced_house_prices_eda.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_us_census} top />
                <CardBody>
                    <CardTitle tag="h5">
                        FastAPI Tensorflow Prediction Server
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>FastAPI server</li>
                            <li>United States Census Income Dataset</li>
                            <li>Tensorflow saved model</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/tree/main/fastapi-tf-prediction-server")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_hackernews} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Hacker News Title Classification with Fastapi
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>hacker news title text dataset</li>
                            <li>sequence model method</li>
                            <li>fastapi prediciton server</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/gcp-custom-ai-models/title_classification.ipynb")}>
                        Github
                    </Button>
                    <Button className="ml-2" color="primary" size="sm" href="tensorflow/title-classification/prediction">
                        Prediction Demo
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_auto_mpg} top />
                <CardBody>
                    <CardTitle tag="h5">
                        Auto Fuel Efficiency Prediction With Vertex AI Custom Container
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Auto MPG dataset</li>
                            <li>Vertex AI Custom Docker Container</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/tree/main/kaggle-auto-mpg-vertex-ai")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_stackoverflow} top />
                <CardBody>
                    <CardTitle tag="h5">
                        StackOverflow Question Topic Classification
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Small subset of stackoverflow questions text dataset</li>
                            <li>TextVectorization layer</li>
                            <li>Model export</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/topic_classification_stackoverflow/topic_classification_stackoverflow.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_cats_and_dogs} top />
                <CardBody>
                    <CardTitle tag="h5">
                        CNN Cats and Dogs Classification
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Cats and Dogs dataset</li>
                            <li>Data augmentation with ImageDataGenerator</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/cnn_cats_and_dogs/cnn_classification_cats_and_dogs.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_fashion} top />
                <CardBody>
                    <CardTitle tag="h5">
                        CNN Fashion MNIST Classification
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Fashion MNIST dataset</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/fashion_mnist_cnn/fashion_mnist_cnn.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

        <Col className="mb-3 col-xs-6">
            <Card style={cardStyle}>
                <CardImg src={img_sign} top />
                <CardBody>
                    <CardTitle tag="h5">
                        CNN Sign MNIST Image Classification
                    </CardTitle>
                    <CardText tag="p">
                        <ul>
                            <li>Sign MNIST dataset</li>
                            <li>Data augmentation with ImageDataGenerator</li>
                        </ul>
                    </CardText>
                    <Button color="primary" size="sm" onClick={() => window.open("https://github.com/techckh/tensorflow-notebooks/blob/main/cnn_sign_language/cnn_classification_sign_mnist.md")}>
                        Github
                    </Button>
                </CardBody>
            </Card>
        </Col>

    </Row>

  </Fragment>
  );
};

export default TensorflowIndex;
