import React, { Fragment, useEffect, useState } from 'react';
import { Card, CardBody, CardColumns, CardTitle, CardText, Nav, NavItem } from 'reactstrap';
import classNames from 'classnames';
import fetch from "cross-fetch";
import FalconCardHeader from '../components/common/FalconCardHeader';
import { InlineTex } from 'react-tex';


const FlashcardTex = (props) => {
    const {index, color, side_a, side_b, data} = props
    const [toggle, setToggle] = useState(false);
    const [text, setText] = useState(side_a);
    
    useEffect(() => {
    }, [])

    function buildSideB(content_str) {
        //content_str = content_str.replace(/\\n/i, '<br/>');
        let lines = content_str.split("\n");
        let out = [];
        lines.forEach((line, index) => {
            out.push(<div><InlineTex texContent={line} /></div>);
        });
        return (<div>{out}</div>);
        //return (<div dangerouslySetInnerHTML={{__html: content_str}} />)
    }

    var sideB = buildSideB(side_b);

    function onClick() {
      if (toggle === false) {
        setText(side_b);
      } else {
        setText(side_a);
      }
      setToggle(!toggle);
    }

    //<div dangerouslySetInnerHTML={{__html: side_a}}></div>
    //let latexString = "\\int_{a}^{b} f(x)dx = F(b) - F(a)";
    return (
        <Card body inverse color={color} className={classNames({'bg-dark':color === 'light'})} style={{cursor:"pointer", width:'80%'}}>
            <CardBody onClick={() => onClick()}>
                <CardTitle>
                    <div style={{color:"#4c535e", fontSize:"1rem"}}>{index}.</div>
                </CardTitle>
                <CardTitle>
                    <InlineTex texContent={side_a}/>
                </CardTitle>
                <CardText>
                    {toggle? sideB : <Fragment></Fragment>}
                </CardText>
            </CardBody>
        </Card>
    )
}


